<template>
  <div>
    <v-col v-if="artList.length">
      <v-card
        class="mx-3"
        v-for="item in artList"
        :key="item.ID"
        link
        @click="$router.push(`detail/${item.ID}`)"
      >
        <v-row no-gutters>
          <v-col class="d-flex justify-center align-center mx-3 ml-6" cols="1">
            <v-img
              :src="item.img"
              max-width="100"
              height="70%"
              class="rounded-lg"
            ></v-img>
          </v-col>
          <v-col>
            <v-card-title class="my-2">
              <v-chip color="pink" label class="cate white--text mr-3">{{
                item.Category.name
              }}</v-chip>
              {{ item.title }}
            </v-card-title>
            <v-card-subtitle>{{ item.desc }}</v-card-subtitle>
            <v-divider></v-divider>
            <v-card-text>
              <v-icon>{{ "mdi-calender-month" }}</v-icon>
              <span>{{ item.CreatedAt | dateformat("YYYY-MM-DD HH:SS") }}</span>
            </v-card-text>
          </v-col>
        </v-row>
      </v-card>
      <div class="text-center">
        <v-pagination
          total-visible="6"
          v-model="queryParams.pagenum"
          :length="Math.ceil(this.total / queryParams.pagesize)"
          @input="getArtByCate($store.getters.getCid)"
        ></v-pagination>
      </div>
    </v-col>
    <v-col width="60vw" height="90vh" v-else>
      <v-img
        src="../assets/none.png"
        class="rounded-lg my-3"
        height="100%"
        width="100%"
      ></v-img
    ></v-col>
  </div>
</template>

<script>
export default {
  data() {
    return {
      artList: [],
      queryParams: {
        title: "",
        pagesize: 5,
        pagenum: 1,
      },
      cid: this.$store.getters.getCid,
      total: 0,
    };
  },
  created() {
    this.$bus.$on("changeCid", (cid) => {
      this.queryParams.pagenum = 1;
      this.queryParams.pagesize = 5;
      if (cid == 0) {
        this.getArtList();
        return;
      }
      this.cid = cid;
      this.getArtByCate(cid);
    });
    this.$bus.$on("searchArt", (value) => {
      this.queryParams.pagenum = 1;
      this.queryParams.pagesize = 5;
      this.queryParams.title = value;
      this.$store.commit("changeCid", 0);
      this.getArtList();
    });
    this.getArtByCate(this.$store.getters.getCid);
  },
  methods: {
    //查询分类下的文章
    async getArtByCate(cid) {
      if (this.$store.getters.getActionType == 1) {
        this.queryParams.title = "";
      } else if (this.$store.getters.getActionType == 2) {
        cid == 0;
      }
      if (cid == 0) {
        this.getArtList();
        return;
      }
      if (cid != this.$store.getters.getCid) {
        this.queryParams.pagenum = 1;
        this.queryParams.pagesize = 5;
      }
      const { data: res } = await this.$http.get(`article/list/${cid}`, {
        params: this.queryParams,
      });
      if (res.status != 200) return this.$message("error", res.message);
      this.artList = res.data;
      this.total = res.total;
    },
    //获取文章列表
    async getArtList() {
      const { data: res } = await this.$http.get("article", {
        params: this.queryParams,
      });
      if (res.status != 200) return this.$message("error", res.message);
      this.artList = res.data;
      this.total = res.total;
    },
  },
};
</script>
<style>
.cate {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  min-width: 80px;
}
</style>